/* navbar styling */
.navbar1{ 
  background: #000;
  padding: 10px;
}
.nav-link:hover{
  text-decoration: underline 2px cyan;
  cursor: pointer;
 
}
.d-block{
  height: 580px;
}

/* Second page styling */
.container1{
  box-shadow: 0px 0px 102px 20px black;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;


}
.text-body-secondary{
  font-size: 24px;
  font-family: 'Courier New', Courier, monospace;
 
}
.carousel{
  margin-top: -10px;
}
.carousel-item{
  height: 600px;
  align-items: center;
}
 

button.btn{
  border: 1px solid orange;
}
button.btn:hover{
  background: #000;
  color: orange;
  border: 1px solid white;
}
input:hover{
  color: white;
}

/* about section */
.cards{
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}
.card{
  box-shadow: 0px 0px 102px 2px rgb(152, 113, 113);
}
.card-img-top{
  height: 500px;
  width: 600px;
}
.card-body{
  background: #000;
  
}
.card-body h5{
  color: azure;
}
.card-body p{
  color: rgb(247, 206, 131);
}
.about{
  height: 700px;
  background: linear-gradient(to right, rgb(44, 40, 40),rgb(19, 19, 19));
}
.head2{
  color: orange;
  font-size: 2rem;
  text-align:center;
  font-family: 'Courier New', Courier, monospace;
}


/* contact page styling */
.form{
  background-image: url(https://www.mindlabpro.com/cdn/shop/articles/Nootropics_for_Bodybuilding_1460x1043_crop_bottom.jpg?v=1583863634);
  background-repeat: no-repeat;
  background-position:center;
  background-size: cover;
  font-size: large;
  height: 600px;
  box-shadow: 0px 0px 20px 12px rgb(255, 255, 255);
  text-align: center;
  color: aliceblue;
  font-size:large;
  margin-top: -20px;

}
.head1{
  color:lemonchiffon;
  font-size: 2rem;
  text-decoration: underline 1px;
}
.form1{
  margin-top: 3rem;
  color: rgb(255, 194, 82);
  font-family: 'Courier New', Courier, monospace;
  display: flex;
  align-items:flex-start;
  justify-content:last baseline;
  flex-wrap: wrap;
  padding: 2px;
  margin-left:4%;
  margin-right: 4%;
  
}
input{
  width: 680px;
  height: 2rem;

}
input:hover{
  background:none;
  color: orange;
}
.btn{
  width: 120px;
  padding:6px;
  background: black;
  color: rgb(255, 194, 82);
  font-size: 1em;
  border: 1px solid orange;
  justify-content: center;
  border-radius: 12px;
  font-family: 'Courier New', Courier, monospace;


}
.btn:hover{
  background:orange;
  color: black;
}
.para1{
  margin-top: 12px;
  color: aliceblue;
} 
@media screen and (max-width: 700px) {
  .form1{
    font-size: medium;
  }
  .form input{
    width: 100%;
  }
 }
  

/* Member page styling */

.row {
  height: 100%;
  align-items: center;
}

#pricing {
  height: 100%;
  padding: 100px;
  text-align: center;
}

.card {
  border: 1px solid black;
}

.card-header {
  color: white;
  background-color: black;
}

.pricing-column {
  padding: 3% 2%;
}

.btn-dark {
  
  color: orange;
}

.btn-dark:hover {
  background-color: #343a40;
}

/* Footer page styling */

.site-footer
{
  background-color:#26272b;
  padding:45px 0 20px;
  font-size:15px;
  line-height:24px;
  color:#ffffff;
}
.site-footer hr
{
  border-top-color:#ffffff;
  opacity:0.5
}
.site-footer hr.small
{
  margin:20px 0
}
.site-footer h6
{
  color:#fff;
  font-size:16px;
  text-transform:uppercase;
  margin-top:5px;
  letter-spacing:2px
}
.site-footer a
{
  color:#ffffff;
}
.site-footer a:hover
{
  color:#ffffff;
  text-decoration:none;
}
.footer-links
{
  padding-left:0;
  list-style:none;
}
.footer-links li
{
  display:block;
  
}
.footer-links a
{
  color:orange;
  text-decoration: none;
}
.footer-links a:active,.footer-links a:focus,.footer-links a:hover
{
  color:#fcfdff;
  text-decoration:none;
}
.footer-links.inline li
{
  display:inline-block
}
.site-footer .social-icons
{
  text-align:right
}
.site-footer .social-icons a
{
  width:40px;
  height:40px;
  line-height:40px;
  margin-left:6px;
  margin-right:0;
  border-radius:100%;
  background-color:#33353d
}
.copyright-text
{
  margin:0
}
@media (max-width:991px)
{
  .site-footer [class=col-]
  {
    margin-bottom:30px
  }
}
@media (max-width:767px)
{
  .site-footer
  {
    padding-bottom:0
  }
  .site-footer .copyright-text,.site-footer .social-icons
  {
    text-align:center
  }
  
  .team{
   display: inline;
   
  }
  .about{
    display: inline;
  }
}
.social-icons
{
  padding-left:0;
  margin-bottom:0;
  list-style:none
}
.social-icons li
{
  display:inline-block;
  margin-bottom:4px
}
.social-icons li.title
{
  margin-right:15px;
  text-transform:uppercase;
  color:#96a2b2;
  font-weight:700;
  font-size:13px
}
.social-icons a{
  background-color:#eceeef;
  color:#818a91;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  transition:all .2s linear;
}
.social-icons a:active,.social-icons a:focus,.social-icons a:hover
{
  color:#fff;
  background-color:#29aafe
}
.social-icons.size-sm a
{
  line-height:34px;
  height:34px;
  width:34px;
  font-size:14px
}
.social-icons a.facebook:hover
{
  background-color:#3b5998
}
.social-icons a.twitter:hover
{
  background-color:#00aced
}
.social-icons a.linkedin:hover
{
  background-color:#007bb6
}
@media (max-width:767px)
{
  .social-icons li.title
  {
    display:block;
    margin-right:0;
    font-weight:600
  }
  .card-body{
   font-size: small;
  }
}